* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  height: min-content;
  margin-top: 30px;
  margin-left: 30px;
}
.country-list {
  list-style: none;
  padding: 0;
  margin-left: 30px;
}

.country__list-item {
  margin-right: 30px;
  margin-bottom: 30px;
}

.country__list-name {
  font-size: 30px;
  font-weight: 700;
  margin: 0;
}

.country__name {
  font-size: 30px;
  font-weight: 700;
  margin: 0;
}
.country-info p {
  margin: 0;
}
.info__value {
  font-size: 20px;
  font-weight: 700;
}
